<template>
  <div id="table-field" class="q-my-md">
    <div class="row items-center q-mb-sm">
      <FormFieldLabel label="Headers" />
      <div class="hide-headers" @click="showHeader = !showHeader">
        <BaseIcon
          :name="showHeader ? 'eva-eye-off-outline' : 'eva-eye-outline'"
          inherit-color
          class="icon"
          size="16px"
        ></BaseIcon>
        <FormFieldLabel
          :label="showHeader ? 'Hide auto-generated headers' : '5 Hidden'"
        />
      </div>
    </div>

    <!-- table -->

    <BaseScrollbar class="q-pb-sm">
      <table>
        <thead>
          <tr>
            <th class="extra-small"></th>
            <th v-for="column in tableColumns" :key="column.id" class="medium">
              {{ column.label }}
            </th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="row in rows" :key="row.id">
            <td>
              <CheckboxField
                v-if="row.showCheckBox"
                v-model="row.hasHeader"
                label=""
                description=""
                class="q-ml-md"
              />
            </td>
            <td v-for="column in tableColumns" :key="column.id">
              <ValidationProvider
                v-slot="{ errors }"
                :name="column.label"
                :rules="{ required: true }"
              >
                <TextField
                  v-model="row[column.value]"
                  is-mandatory
                  :error="errors[0]"
                />
              </ValidationProvider>
            </td>
          </tr>
        </tbody>
      </table>
    </BaseScrollbar>

    <!-- ... -->

    <FormFieldError v-if="error" :error="error" />
  </div>
</template>

<script>
import { ValidationProvider } from "vee-validate";
// import TableField from "@/components/common/form/table-field/TableField.vue";
import FormFieldLabel from "@/components/common/form/FormFieldLabel.vue";
import FormFieldError from "@/components/common/form/FormFieldError.vue";
import TextField from "@/components/common/form/text-field/TextField.vue";
import CheckboxField from "@/components/common/form/checkbox-field/CheckboxField.vue";

export default {
  name: "Headers",

  components: {
    ValidationProvider,
    FormFieldLabel,
    FormFieldError,
    TextField,
    CheckboxField,
  },

  data() {
    return {
      tableColumns: [
        {
          id: this.$nano.id(),
          label: "Key",
          value: "Key",
        },
        {
          id: this.$nano.id(),
          label: "Value",
          value: "Value",
        },
        {
          id: this.$nano.id(),
          label: "Description",
          value: "Description",
        },
      ],
      rows: [
        {
          id: this.$nano.id(),
          Key: "",
          Value: "",
          Description: "",
        },
      ],
      error: "",
      showHeader: false,
    };
  },

  watch: {
    showHeader() {
      if (this.showHeader) {
        this.rows = [
          {
            id: this.$nano.id(),
            Key: "Host",
            Value: "<calculated when request is sent>",
            Description: "",
            showCheckBox: true,
            hasHeader: true,
          },
          {
            id: this.$nano.id(),
            Key: "User-Agent",
            Value: "PostmanRuntime/7.34.0",
            Description: "",
            showCheckBox: true,
            hasHeader: true,
          },
          {
            id: this.$nano.id(),
            Key: "Accept",
            Value: "*/*",
            Description: "",
            showCheckBox: true,
            hasHeader: true,
          },

          {
            id: this.$nano.id(),
            Key: "Accept-Encoding",
            Value: "gzip, deflate, br",
            Description: "",
            showCheckBox: true,
            hasHeader: true,
          },

          {
            id: this.$nano.id(),
            Key: "Connection",
            Value: "keep-alive>",
            Description: "",
            showCheckBox: true,
            hasHeader: true,
          },

          {
            id: this.$nano.id(),
            Key: "",
            Value: "",
            Description: "",
            showCheckBox: false,
            hasHeader: false,
          },
        ];
      } else {
        this.rows = [
          {
            id: this.$nano.id(),
            Key: "",
            Value: "",
            Description: "",
          },
        ];
      }
    },
  },

  methods: {},
};
</script>

<style lang="scss">
#table-field {
  table {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;

    tr {
      height: 40px;
    }

    th {
      padding: 4px 4px;
      text-align: left;
      font-weight: 500;
      text-transform: capitalize;

      &.extra-small {
        width: 20px;
      }

      &.small {
        width: 70px;
      }

      &.medium {
        width: 140px;
      }

      &.large {
        width: 210px;
      }

      &.actions {
        width: 48px;
      }
    }

    th,
    td {
      border: 1px solid var(--divider-color);
    }

    td {
      padding: 4px;
      vertical-align: top;
    }

    td .label {
      display: flex;
      align-items: center;
      height: 40px;
      padding-left: 4px;
      color: var(--icon-color);
    }
  }

  .hide-headers {
    border: 1px solid #f9f9f9;
    border-radius: 35px;
    background: #f9f9f9;
    margin-left: 8px;
    // height: 20px;
    // border-radius: 35px;
    // padding: 4px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 28px;
    padding: 6px;
    padding-top: 12px;
    cursor: pointer;

    .icon {
      color: var(--icon-color-inverted);
      padding-bottom: 8px;
      margin-right: 4px;
    }
  }
}
</style>
